html,
body {
	padding: 0;
	margin: 0;
	font-family: Poppins;
}

.land {
	button {
		background-color: #1e45db;
		color: #fff;
		cursor: pointer;
		border: 2px solid #1e45db;
		border-radius: 4px;
		padding: 8px;
		font-size: 12px;
	}

	button:hover {
		background-color: #012fb7;
		border-color: #012fb7;
	}

	button:focus,
	button:focus-visible {
		border-color: #012fb7;
		outline: -webkit-focus-ring-color auto 0px;
	}
}

.center {
	flex-direction: column;
